package com.jine.zouwedding.ui.sections

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.*
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.clip
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp
import com.jine.zouwedding.data.UserModel
import com.jine.zouwedding.ui.views.Body
import com.jine.zouwedding.ui.views.VerticalSpacing

@Composable
fun Maps(height: Dp, user: UserModel) {
    Section(height = height, title = "Ça se passe où ?") {
        Row(modifier = Modifier.fillMaxWidth(), horizontalArrangement = Arrangement.spacedBy(32.dp)) {
            if (user.isInvitedFriday) {
                Column(modifier = Modifier.weight(1f)) {
                    Body(
                        content = "Vendredi",
                        fontWeight = FontWeight.Bold
                    )
                    VerticalSpacing(24.dp)
                    MapImage()
                }
            }
            Column(modifier = Modifier.weight(1f)) {
                Body(
                    content = if (user.isInvitedSunday) "Samedi & Dimanche" else "Samedi",
                    fontWeight = FontWeight.Bold
                )
                VerticalSpacing(24.dp)
                MapImage()
            }
        }

        if (user.isInvitedFullSaturday) {
            VerticalSpacing(32.dp)
            Body(
                content = "Besoin de dormir dans les environs ?",
                fontWeight = FontWeight.Bold,
                modifier = Modifier.padding(top = 24.dp)
            )
            VerticalSpacing(24.dp)
            Body(content = "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.")
            VerticalSpacing(24.dp)
            MapImage()
        }
    }
}

//TODO place real images
@Composable
private fun MapImage() {
    Box(
        modifier = Modifier
            .fillMaxWidth()
            .height(300.dp)
            .clip(RoundedCornerShape(20.dp))
            .background(color = Color.Red)
    )
}

