import androidx.compose.runtime.*
import androidx.compose.ui.ExperimentalComposeUiApi
import androidx.compose.ui.window.CanvasBasedWindow
import com.jine.zouwedding.HomePage
import com.jine.zouwedding.LoginPage
import com.jine.zouwedding.data.UserModel
import org.jetbrains.skiko.wasm.onWasmReady

@OptIn(ExperimentalComposeUiApi::class)
fun main() {
    onWasmReady {
        CanvasBasedWindow("ZouWedding") {
            // todo uncomment the following line to apply the real behavior
            var user by remember { mutableStateOf<UserModel?>(null) }
            // todo comment the following line to apply the real behavior
            //var user = UserModel.getFakeUser()
            user?.let {
                HomePage(it)
            } ?: run {
                LoginPage(onUserLogged = {
                    user = it
                })
            }
        }
    }
}
