package com.jine.zouwedding.ui.sections

import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.ColumnScope
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.height
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.Text
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.Dp
import com.jine.zouwedding.ui.views.Body
import com.jine.zouwedding.ui.views.SectionTitle
import com.jine.zouwedding.ui.views.VerticalSpacing
import com.jine.zouwedding.ui.views.horizontalDefaultPadding

@Composable
fun Section(
    height: Dp,
    title: String,
    content: String? = null,
    customContent: (@Composable ColumnScope.() -> Unit)? = null
) {
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .horizontalDefaultPadding()
    ) {
        VerticalSpacing(height / 5)
        SectionTitle(title)
        VerticalSpacing(height / 14)
        content?.let {
            Body(content = content)
        }
        customContent?.invoke(this)
        VerticalSpacing(height / 5)
    }
}