package com.jine.zouwedding.ui.sections

import androidx.compose.foundation.layout.*
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.RadioButton
import androidx.compose.material3.Switch
import androidx.compose.material3.Text
import androidx.compose.runtime.*
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp
import com.jine.zouwedding.data.FoodModel
import com.jine.zouwedding.data.UserModel
import com.jine.zouwedding.ui.views.Body
import com.jine.zouwedding.ui.views.HorizontalSpacing
import com.jine.zouwedding.ui.views.VerticalSpacing
import com.jine.zouwedding.ui.views.WeddingButton

@Composable
fun Form(height: Dp, userModel: UserModel, onChoiceUpdated: (UserModel) -> Unit) {
    var user by remember { mutableStateOf(userModel) }
    Section(height = height, title = "C'est le moment de t'exprimer !", customContent = {
        PlanningForm(user) {
            user = it
        }
        FoodForm(user) {
            user = it
        }
        Box(modifier = Modifier.fillMaxWidth(), contentAlignment = Alignment.Center) {
            WeddingButton("Sauvegarder") {
                onChoiceUpdated(user)
            }
        }
    })
}

@Composable
private fun PlanningForm(user: UserModel, onChoiceUpdated: (UserModel) -> Unit) {
    Column(modifier = Modifier.padding(bottom = 64.dp).fillMaxWidth(0.6f)) {
        FormTitle(title = "Est-que tu viens ?")
        if (user.isInvitedFriday) {
            PlanningAnswer(
                label = "Le vendredi à la maire",
                isChecked = user.joinFriday,
                onAnswered = {
                    user.joinFriday = it
                    onChoiceUpdated(user)
                })
        }
        if (user.isInvitedFullSaturday) {
            PlanningAnswer(
                label = "Le samedi toute la journée",
                isChecked = user.joinFullSaturday,
                onAnswered = {
                    user.joinFullSaturday = it
                    onChoiceUpdated(user)
                })
        } else {
            PlanningAnswer(
                label = "Le samedi pour le cocktail",
                isChecked = user.joinCocktail,
                onAnswered = {
                    user.joinCocktail = it
                    onChoiceUpdated(user)
                })
        }
        if (user.isInvitedSunday) {
            PlanningAnswer(
                label = "Le dimanche pour le brunch",
                isChecked = user.joinSunday,
                onAnswered = {
                    user.joinSunday = it
                    onChoiceUpdated(user)
                })
        }
    }
}

@Composable
private fun PlanningAnswer(label: String, isChecked: Boolean, onAnswered: (Boolean) -> Unit) {
    var isAnswerChecked by remember { mutableStateOf(isChecked) }
    Row(verticalAlignment = Alignment.CenterVertically, modifier = Modifier.fillMaxWidth()) {
        Body(content = label)
        HorizontalSpacing(1f)
        Text(
            text = "Je ne peux pas..",
            style = MaterialTheme.typography.labelSmall,
            fontWeight = FontWeight.Normal
        )
        HorizontalSpacing(6.dp)
        Switch(checked = isAnswerChecked, onCheckedChange = {
            isAnswerChecked = it
            onAnswered(it)
        })
        HorizontalSpacing(6.dp)
        Text(
            text = "Avec plaisir !",
            style = MaterialTheme.typography.labelSmall,
            fontWeight = FontWeight.Normal
        )
    }
}

@Composable
private fun FoodForm(user: UserModel, onChoiceUpdated: (UserModel) -> Unit) {
    if (user.isInvitedFullSaturday) {
        Column(modifier = Modifier.padding(bottom = 64.dp)) {
            FormTitle(title = "Et tu mangeras quoi ?")
            FoodAnswer(
                selectedFood = user.food,
                onFoodSelected = {
                    user.food = it
                    onChoiceUpdated(user)
                })
        }
    }
}

@Composable
private fun FoodAnswer(selectedFood: String?, onFoodSelected: (String) -> Unit) {
    val options = FoodModel.getAll()
    var selectedOption by remember { mutableStateOf(selectedFood) }
    Row(modifier = Modifier.fillMaxWidth(), horizontalArrangement = Arrangement.SpaceEvenly) {
        options.forEach { foodOption ->
            Column(horizontalAlignment = Alignment.CenterHorizontally) {
                RadioButton(
                    selected = (foodOption == FoodModel.findFrom(selectedOption)),
                    onClick = {
                        selectedOption = foodOption.name
                        onFoodSelected(foodOption.name)
                    }
                )
                VerticalSpacing(8.dp)
                Body(content = foodOption.getLabel())
            }
        }
    }
}

@Composable
private fun FormTitle(title: String) {
    Text(
        text = title,
        style = MaterialTheme.typography.titleLarge,
        fontWeight = FontWeight.Bold,
        modifier = Modifier.padding(bottom = 32.dp)
    )
}