package com.jine.zouwedding

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.material3.Button
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.Text
import androidx.compose.material3.TextField
import androidx.compose.runtime.*
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.input.key.Key
import androidx.compose.ui.input.key.key
import androidx.compose.ui.input.key.onKeyEvent
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.text.style.TextAlign
import androidx.compose.ui.unit.dp
import com.jine.zouwedding.data.UserModel
import com.jine.zouwedding.ui.theme.AppTheme
import com.jine.zouwedding.ui.views.VerticalSpacing

@Composable
fun LoginPage(viewModel: LoginViewModel = LoginViewModel(), onUserLogged: (user: UserModel) -> Unit) =
    AppTheme {
        val data by viewModel.state.collectAsState()
        val verticalSpacingValue = 48.dp

        if (data is LoginViewModel.State.Success) {
            onUserLogged((data as LoginViewModel.State.Success).user)
        }

        Column(
            modifier = Modifier.fillMaxSize(),
            horizontalAlignment = Alignment.CenterHorizontally,
            verticalArrangement = Arrangement.Center
        ) {
            Text("Mariage des Zous", fontWeight = FontWeight.Black, style = MaterialTheme.typography.displayLarge)
            VerticalSpacing(verticalSpacingValue)
            Text(
                "Bienvenue sur le site du mariage des Zous !\n" +
                        "Tu peux renseigner ton prénom et ton nom pour accéder au contenu du site",
                textAlign = TextAlign.Center
            )
            VerticalSpacing(verticalSpacingValue)
            var textFieldValue by remember { mutableStateOf("") }
            TextField(
                singleLine = true,
                modifier = Modifier.onKeyEvent {
                    if (it.key == Key.Enter) {
                        viewModel.login(textFieldValue)
                        true
                    } else {
                        false
                    }
                },
                value = textFieldValue,
                onValueChange = { textFieldValue = it },
                placeholder = {
                    Text("Entre ton prénom et ton nom")
                })
            VerticalSpacing(verticalSpacingValue)
            Button(
                onClick = { viewModel.login(textFieldValue) },
                enabled = textFieldValue.isNotBlank() && textFieldValue.isNotBlank()
            ) {
                Text("C’est partit !")
            }
            VerticalSpacing(verticalSpacingValue)
            Text(
                when (data) {
                    is LoginViewModel.State.Error -> (data as LoginViewModel.State.Error).message
                    is LoginViewModel.State.Loading -> "Loading..."
                    else -> ""
                }
            )
        }
    }