package com.jine.zouwedding.data

import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable
data class UserModel(
    val id: Int,
    val firstname: String,
    val lastname: String,
    @SerialName("has_answered")
    var hasAnswered: Boolean,
    @SerialName("join_friday")
    var joinFriday: Boolean,
    @SerialName("join_full_saturday")
    var joinFullSaturday: Boolean,
    @SerialName("join_sunday")
    var joinSunday: Boolean,
    @SerialName("join_cocktail")
    var joinCocktail: Boolean,
    @SerialName("is_invited_friday")
    var isInvitedFriday: Boolean,
    @SerialName("is_invited_full_saturday")
    var isInvitedFullSaturday: Boolean,
    @SerialName("is_invited_sunday")
    var isInvitedSunday: Boolean,
    var food: String?
) {

    companion object {
        fun getFakeUser(): UserModel = UserModel(
            id = 0,
            firstname = "Maxime",
            lastname = "Maraval",
            hasAnswered = false,
            joinFriday = false,
            joinFullSaturday = false,
            joinSunday = false,
            joinCocktail = false,
            isInvitedFriday = true,
            isInvitedFullSaturday = true,
            isInvitedSunday = true,
            food = null
        )
    }
}