@Composable
internal fun HomePage(user: UserModel, viewModel: WeddingViewModel = WeddingViewModel()) = AppTheme {

    // notification of saving for the form
    val notification by viewModel.choiceUpdateNotification.collectAsState()

    val coroutineScope = rememberCoroutineScope()

    val snackbarHostState = remember { SnackbarHostState() }
    val lazyListState = rememberLazyListState()

    Scaffold(snackbarHost = { SnackbarHost(snackbarHostState) }) {
        Box(modifier = Modifier.fillMaxSize()) {
            ScrollableContent(
                modifier = Modifier.padding(top = 24.dp),
                user = user,
                coroutineScope = coroutineScope,
                lazyListState = lazyListState,
                onUserUpdated = { userUpdated ->
                    viewModel.updateGuestChoice(userUpdated)
                })
            TopAppBar(coroutineScope = coroutineScope, lazyListState = lazyListState)
        }
    }

    notification?.let {
        coroutineScope.launch {
            showNotification(it, snackbarHostState)
            viewModel.notificationHasBeenConsumed()
        }
    }
}

@Composable
private fun TopAppBar(coroutineScope: CoroutineScope, lazyListState: LazyListState) {
    Row(
        modifier = Modifier
            .fillMaxWidth()
            .background(
                brush = Brush.verticalGradient(
                    colors = listOf(
                        MaterialTheme.colorScheme.background,
                        MaterialTheme.colorScheme.background,
                        MaterialTheme.colorScheme.background,
                        Color.Transparent
                    )
                )
            )
            .padding(top = 12.dp, bottom = 24.dp, start = 24.dp, end = 24.dp),
        horizontalArrangement = Arrangement.spacedBy(24.dp),
        verticalAlignment = Alignment.CenterVertically
    ) {
        val interactionSource = remember { MutableInteractionSource() }
        Text(
            text = "Mariage des Zous",
            style = MaterialTheme.typography.bodyLarge,
            fontWeight = FontWeight.Black,
            modifier = Modifier.clickable(interactionSource = interactionSource, indication = null) {
                coroutineScope.launch {
                    lazyListState.animateScrollToItem(0)
                }
            })
        HorizontalSpacing(1f)
        TopAppBarDefaultButton("Planning") {
            coroutineScope.launch {
                lazyListState.animateScrollToItem(1)
            }
        }
        TopAppBarDefaultButton("Thème") {
            coroutineScope.launch {
                lazyListState.animateScrollToItem(2)
            }
        }
        TopAppBarDefaultButton("Cadeaux") {
            coroutineScope.launch {
                lazyListState.animateScrollToItem(3)
            }
        }
        TopAppBarDefaultButton("Le lieu") {
            coroutineScope.launch {
                lazyListState.animateScrollToItem(4)
            }
        }
        TopAppBarPrimaryButton("Je viens !") {
            coroutineScope.launch {
                lazyListState.animateScrollToItem(5)
            }
        }
    }
}

@OptIn(ExperimentalComposeUiApi::class)
@Composable
private fun ScrollableContent(
    user: UserModel,
    coroutineScope: CoroutineScope,
    lazyListState: LazyListState,
    modifier: Modifier = Modifier,
    onUserUpdated: (UserModel) -> Unit
) {
    val height = (LocalWindowInfo.current.containerSize.height).dp / 2.1f //todo need to fix this

    LazyColumn(modifier = modifier, state = lazyListState) {
        item {
            Header(height = height, user = user, onRSVPButtonClicked = {
                coroutineScope.launch {
                    lazyListState.animateScrollToItem(5)
                }
            }, onScrollClicked = {
                coroutineScope.launch {
                    lazyListState.animateScrollToItem(1)
                }
            })
        }
        item {
            Planning(height = height, user = user)
        }
        item {
            Theme(height = height)
        }
        item {
            WeddingList(height = height)
        }
        item {
            Maps(height = height, user = user)
        }
        item {
            Form(height = height, userModel = user, onChoiceUpdated = onUserUpdated)
        }
    }
}

private suspend fun showNotification(
    notification: WeddingViewModel.ChoiceUpdateNotification, snackbarHostState: SnackbarHostState
) {
    snackbarHostState.showSnackbar(
        message = if (notification is WeddingViewModel.ChoiceUpdateNotification.Success) {
            "Choix mis à jour avec succès"
        } else {
            "Erreur dans la mise à jour du choix"
        }, duration = SnackbarDuration.Short
    )
}

