package com.jine.zouwedding

import com.jine.zouwedding.data.FoodModel
import com.jine.zouwedding.data.UserModel
import io.ktor.client.*
import io.ktor.client.call.*
import io.ktor.client.request.*
import io.ktor.client.statement.*
import io.ktor.http.*
import io.ktor.utils.io.core.*
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.launch
import kotlinx.serialization.Serializable

class LoginViewModel : ZouViewModel() {

    private val _state = MutableStateFlow<State?>(null)
    val state: StateFlow<State?> get() = _state.asStateFlow()

    sealed interface State {
        data object Loading : State
        data class Success(val user: UserModel) : State
        data class Error(val message: String) : State
    }

    fun login(userInput: String) {
        _state.value = State.Loading
        val userInputValues = userInput.trim().split(" ")
        val treatedUserInput1 =
            "$${userInputValues.getOrElse(0) { "" }}${userInputValues.getOrElse(1) { "" }}".toLowerCase().normalize()
        val treatedUserInput2 =
            "$${userInputValues.getOrElse(1) { "" }}${userInputValues.getOrElse(0) { "" }}".toLowerCase().normalize()

        viewModelScope.launch {
            createHttpClient().use {
                val response: LoginResponse = makeRequest(it, treatedUserInput1, treatedUserInput2).body()
                when (response.count) {
                    1 -> _state.value = State.Success(response.results.first())//State.Success(getUserFrom(response.results.first()))
                    0 -> _state.value = State.Error("Pas trouvé")
                    else -> _state.value = State.Error("trop de monde corresponds, précise ton nom / prénom")
                }
            }
        }
    }

    private suspend fun makeRequest(
        httpClient: HttpClient, treatedUserInput1: String, treatedUserInput2: String
    ): HttpResponse {
        return httpClient.request("https://api.baserow.io/api/database/rows/table/302843/") {
            method = HttpMethod.Get
            headers {
                append("Authorization", "Token o0pJ9uzhCRFeCHA3KKCpWAiTw68oAR8C")
            }
            url {
                parameters.append("user_field_names", "true")
                parameters.append(
                    "filters",
                    "{\"filter_type\":\"OR\",\"filters\":[{\"type\":\"contains\",\"field\":\"search\",\"value\":\"$treatedUserInput1\"},{\"type\":\"contains\",\"field\":\"search\",\"value\":\"$treatedUserInput2\"}],\"groups\":[]}".replace(
                        "$",
                        ""
                    )
                )
            }
        }
    }

    private fun String.normalize(): String {
        val original = arrayOf("é", "è", "ê", "ë", "î", "ï")
        val normalized = arrayOf("e", "e", "e", "e", "i", "i")

        return this.map {
            val index = original.indexOf(it.toString())
            if (index >= 0) normalized[index] else it
        }.joinToString("")
    }

    @Serializable
    private data class LoginResponse(
        val count: Int, val results: List<UserModel>
    )
}